.hero * {
  box-sizing: border-box;
}

.hero picture, .hero img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
}

.hero {
  background-color: var(--color-black);
  transition: var(--transform-hover-focus);
  color: var(--color-black-text);
  font-style: normal;
  overflow: hidden;
}

body.dark-theme .hero {
  background-color: var(--color-dark-theme);
  color: var(--color-light-theme);
}

.hero .container {
  width: 100%;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
  display: flex;
}

.hero__content {
  width: 100%;
  max-width: 446px;
}

.hero__slider {
  width: 100%;
  max-width: 602px;
}

.hero-title {
  text-transform: uppercase;
  font-size: 36px;
  font-weight: 600;
  line-height: 1.2;
}

.hero-title .green-color {
  color: var(--color-green);
  font-style: italic;
  font-weight: 400;
}

.hero-subtitle {
  max-width: 408px;
  margin-top: 16px;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.5;
  display: inline-block;
}

.hero-button {
  color: inherit;
  cursor: pointer;
  border: 1px solid var(--color-green);
  transition: var(--transform-hover-focus);
  background: none;
  border-radius: 15px;
  outline: none;
  justify-content: center;
  align-items: center;
  margin-top: 32px;
  padding: 14px 28px;
  font-size: 14px;
  font-weight: 500;
  line-height: normal;
  text-decoration: none;
  display: inline-flex;
}

.hero-button:hover, .hero-button:focus {
  background: var(--color-green);
  color: var(--color-light-theme);
}

